@charset "UTF-8";

/* responsive font size
   ------------------------------------------------------ */
@import "../../node_modules/rfs/scss";
$rfs-base-value: 1rem;

/* variables
   ------------------------------------------------------ */
$font-bk: #555555;
/* ---
可変フォントサイズ用の変数
768〜1024px幅においてフォントサイズを変化させる
--- */
$variable-start: 768;
$variable-end: 1024;

/* ======================================================
   Foundation
   ====================================================== */
html {
  scroll-behavior: smooth;
}
body {
  font-family: "Yu Gothic", "游ゴシック", YuGothic, "游ゴシック体", "ヒラギノ角ゴ Pro W3", "メイリオ", sans-serif;
  color: $font-bk;
}
*,
::before,
::after {
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}
/* default style
   ------------------------------------------------------ */
h1,
h2,
h3,
h4 {
  color: #800080;
  font-family: "Josefin Sans", sans-serif;
  font-weight: 400;
}
.caption {
  font-family: a-otf-midashi-mi-ma31-pr6n, serif;
}

/* ---------- background color ---------- */
.bg-gray {
  background: dimgray;
}
.bg-purple {
  background: purple;
}
.bg-smoke {
  background-color: whitesmoke;
}
/* ---------- font color ---------- */
.font-black {
  color: #555;
}
.font-gray {
  color: darkgray;
}
.font-purple {
  color: purple;
}
.font-white {
  color: white;
}
/* ---------- font size ---------- */
p:not([class]),
li:not([class]),
.font-m {
  $min-rem-p: 0.875;
  $max-rem-p: 1;
  //〜767px
  font-size: $min-rem-p + rem;
  //768px〜
  @media screen and (min-width: 768px) {
    font-size: calc(
      (#{$max-rem-p - $min-rem-p} * 16) * ((100vw - #{$variable-start + px}) / (#{$variable-end - $variable-start})) + #{$min-rem-p +
        rem}
    );
  }
  //1024px〜
  @media screen and (min-width: 1024px) {
    //最大値適用の画面幅の下限
    font-size: $max-rem-p + rem; //最大値
  }
}

.font-xs {
  $min-rem-xs: 0.75;
  $max-rem-xs: 0.875;
  //〜767px
  font-size: $min-rem-xs + rem;
  //768px〜
  @media screen and (min-width: 768px) {
    font-size: calc(
      (#{$max-rem-xs - $min-rem-xs} * 16) * ((100vw - #{$variable-start + px}) / (#{$variable-end - $variable-start})) +
        #{$min-rem-xs + rem}
    );
  }
  //1024px〜
  @media screen and (min-width: 1024px) {
    //最大値適用の画面幅の下限
    font-size: $max-rem-xs + rem; //最大値
  }
}

.font-s {
  $min-rem-s: 0.75;
  $max-rem-s: 0.9375;
  //〜767px
  font-size: $min-rem-s + rem;
  //768px〜
  @media screen and (min-width: 768px) {
    font-size: calc(
      (#{$max-rem-s - $min-rem-s} * 16) * ((100vw - #{$variable-start + px}) / (#{$variable-end - $variable-start})) + #{$min-rem-s +
        rem}
    );
  }
  //1024px〜
  @media screen and (min-width: 1024px) {
    //最大値適用の画面幅の下限
    font-size: $max-rem-s + rem; //最大値
  }
}

.font-l {
  @include font-size(1.5rem);
}

.font-xl {
  @include font-size(2rem);
}

/* ======================================================
   Layout
   ====================================================== */
.l-header {
  margin-top: 1rem;
}
.l-main {
  max-width: 1080px;
  margin: 5rem auto 0;
  @media screen and (min-width: 768px) {
    margin-top: 7rem;
  }
  @media screen and (min-width: 1200px) {
    margin-top: 9rem;
  }
}
.l-section {
  margin-top: 1rem;
  @media screen and (min-width: 768px) {
    margin-top: 4rem;
  }
}
.l-footer {
  margin-top: 4rem;
}

/* ======================================================
   Object
   ====================================================== */

/* Project
------------------------------------------------------ */
/*** 
header
***/
.p-header__container {
  display: flex;
  justify-content: space-between;
  position: relative;
  margin: 0 auto;
  max-width: 95%;
  @media screen and (min-width: 1200px) {
    max-width: 1080px;
  }
}
.p-header__inner {
  position: absolute;
  left: 0;
  right: 0;
  text-align: center;
}
.p-header__global-nav {
  display: flex;
  justify-content: center;
  li:not(:first-child) {
    margin-left: 1.5rem;
  }
  a {
    color: $font-bk;
  }
}

.p-header__shop-nav {
  display: block;
  ul {
    display: flex;
    justify-content: flex-end;
  }
  li {
    margin-left: 20px;
  }
  a {
    color: $font-bk;
  }
}

/**
 * slider
 */
.p-slider-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}
.p-slider-frame {
  position: relative;
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
  & .p-sliderPause {
    position: absolute;
    top: 50%;
    left: 50%;
    padding: 0.125rem 0.5rem;
    transform: translate(-50%, -50%);
    opacity: 0;
    text-align: center;
    background-color: rgba($color: #000000, $alpha: 0.3);
    color: #fff;
    user-select: none;
    transition: opacity 0.5s;
  }
}
.p-slider-wrapper {
  display: flex;
  position: relative;
  left: 0;
  transform: translateX(0%);
  transition: 1s;
}
.p-slider-item {
  position: relative;
  flex: 1 0 100%;
  margin-left: 0;
  left: 0;
  transition: 0s;
  // & .p-slider-text {
  //   position: absolute;
  //   left: 0;
  //   font-size: 5vw;
  //   padding: 0 2vw;
  //   background-color: rgba(0, 0, 0, 0.2);
  //   color: #fff;
  // }
  & img {
    width: 100%;
  }
}
.p-slider-controls {
  position: absolute;
  top: 50%;
  left: 1%;
  right: 1%;
  transform: translateY(-50%);
  margin-top: 1rem;
  .next {
    float: right;
  }
}
.p-slider-button {
  background-color: rgba($color: #000000, $alpha: 0.1);
  padding: 0.25rem 0.5rem;
  outline: rgba($color: #800080, $alpha: 0.1) solid 3px;
  color: rgba($color: #fff, $alpha: 0.8);
  font-size: 1.5rem;
  z-index: 3;
}
/*** 
section共通
***/
.p-section__heading {
  margin: 1rem 0;
}

/***
News section
***/
.p-news__heading {
  padding: 0.25rem 0;
}

@media screen and (min-width: 1200px) {
  .news {
    display: flex;
    justify-content: center;
  }
  .p-news__deck {
    display: flex;
    justify-content: center;
    width: 80%;
  }
}
.p-news__header {
  align-items: stretch;
  display: flex;
  flex: 0 0 20%;
  justify-content: center;
}
.p-news__heading {
  align-self: center;
  color: #fff;
  font-size: 1rem;
  font-weight: normal;
  letter-spacing: 2px;
}
.p-news__body {
  align-items: center;
  display: flex;
  flex: 1 1 0;
  padding: 1rem;
  text-align: left;
}
.p-news__content {
  display: flex;
}
.p-news__content + .p-news__content {
  margin-top: 0.5rem;
}
.p-news__content p {
  display: inline-block;
  margin-left: 1rem;
}

/***
About section
***/

.p-about__list {
  margin: 1rem 0 1rem 0.75rem;
}
.p-about__list li {
  list-style-type: disc;
  list-style-position: inside;
}

.p-about a {
  text-decoration: underline;
}

.p-about__button {
  display: flex;
  justify-content: center;
  position: relative;
  margin: 1rem 0;
  .btn {
    border: 1px solid purple;
    padding: 0.25rem 1rem;
    cursor: pointer;
    transition: 0.4s;
    user-select: none;
    &:hover {
      background: purple;
      color: white;
    }
    &:active {
      opacity: 0.8;
      position: relative;
      top: 1px;
      box-shadow: -1px -1px 5px gray, 1px 1px 5px gray;
    }
  }
}

/*** 
Music section
***/
.p-music__grid {
  display: flex;
  justify-content: space-between;
  margin-top: 1.5rem;
}
.p-music__card {
  width: calc(33.33333% - 16px);
  padding: 1rem 1.5rem;
  box-shadow: -2px 2px 4px 0px #ddd;
  img {
    width: 100%;
  }
}
.p-music__card-heading {
  font-family: "M PLUS 1p", sans-serif;
  font-weight: 500;
}

.p-music__text {
  margin-top: 0.5rem;
}

/***
Foods and Drinks section
***/
.p-foods__grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  margin-top: 1.5rem;
}
.p-foods__grid .card {
  width: calc(25% - 8px);
  img {
    width: 100%;
  }
}

/***
Sights section
***/
.p-sights__grid {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  margin-top: 1.5rem;
}
.p-sights__card {
  margin-bottom: 10px;
  width: calc(20% - 8px);
  img {
    width: 100%;
    min-width: 200px;
  }
}

/*** 
footer
***/
.p-footer {
  padding-bottom: 25px;
  text-align: center;
  &__nav {
    display: flex;
    justify-content: space-around;
    max-width: 720px;
    margin: 0 auto;
    padding-top: 25px;
    width: 80vw;
  }
  & a {
    color: white;
    text-decoration: none;
  }
  &__logo {
    margin: 30px auto 30px;
    max-width: 120px;
    width: 20vw;
  }
}
/*** 
Return top
***/
.return-to-top {
  position: fixed;
  bottom: 30px;
  right: 30px;
  transition: 0.2s;
  color: gray;
  opacity: 0.6;
}
/* Accessibility
   ------------------------------------------------------ */
.screen-reader-text {
  position: absolute;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  width: 1px;
  height: 1px;
}
